    #classicformpage .view {
      background-image: url(/garden_hub2.1/static/media/grassBackground.6d1f7c70.jpeg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: fixed center;
      height: 100vh;
    }
    
    #classicformpage .gradient {
      background: -webkit-linear-gradient(45deg, rgba(0, 255, 0, 0.3), rgba(0, 255, 0, 0.3)  100%);
      background: -webkit-gradient(linear, 45deg, from(rgba(0, 0, 0, 0.7), rgba(72, 15, 144, 0.4) 100%));
      background: -webkit-linear-gradient( 45deg, rgba(0, 0, 0, 0.7), rgba(72, 15, 144, 0.4) 100%);
      background: linear-gradient( 45deg, rgba(0, 0, 0, 0.7), rgba(72, 15, 144, 0.4) 100%);
    }
    
    #classicformpage .card {
      background: -webkit-linear-gradient(45deg, rgba(-17, 94, 45, 0.3), rgba(0, 255, 0, 0.3)  100%);
    }
    
    #classicformpage h6 {
      line-height: 1.7;
    }
    
    #classicformpage .navbar {
      -webkit-transition: background .5s ease-in-out,padding .5s ease-in-out;
      transition: background .5s ease-in-out,padding .5s ease-in-out;
    }
    
    .top-nav-collapse {
      background: black !important;
    }
    
    #classicformpage label {
      color: #fff!important;
    }
    
    
    input.form-control{
      color: white
    }
    .md-form
    input[type=text]{
      color:white;
    }
    .md-form .prefix ~ input, .md-form .prefix ~ textarea{
      color: white
    }

