/* media queries=============================================================================================================== */

@media only screen and (min-width:404px){
  input{
    width: 339px;
  }
  img.login-pic{
    width: 400px;
    height: 200px;
    margin-bottom: -90px;
    margin-top: 50px;
    margin-left: -20px;
  }
}



@media only screen and (max-width: 768px) {
  #classicformpage .navbar:not(.top-nav-collapse) {
      background: transparent !important;
  }
}





